import React, { Component } from 'react';
import Logost from '../components/logo';

import { useEffect, useRef, useState } from "react";


export default function LL(){

const [act,setAct] = useState("rent btn-active");


    return (
        <div>
            <div className='side-logo-con'>
                <div className='logo-container'>
                 <Logost/>
                </div>
                <div className='side-search-con'>
                    <div className='select-rb-con valign-wrapper'>
                        <div className='btn-rb-con'>
                        <a  className={(act=="rent btnactive")?'btn btn-flat  side-btnactive':'btn blue-grey lighten-5 btn-flat  black-text'}>Rent</a>
                        <a  className={(act=="buy btnactive")?'btn btn-flat  side-btnactive':'btn  blue-grey lighten-5 btn-flat  black-text'}>Buy</a>
    
                        </div>
                        <div className='side-select-con'>
                        <select className='side-selectbar'>
                            <option value="">Select City</option>
                            <option value="patna">Patna</option>
                            <option value="jabalpur">Jabalpur</option>
                        </select>
                        </div>
                    </div>
                    <div className='search-rb-con'>
                    <div className='side'>
                            <input type="text" id="search" className='side-searchbar' placeholder="Search "   />
                           
                     </div>
                     <div className='side-search-btn-con'>
                     <button className='btn btn-large btn-flat black side-search-btn white-text'><i className='material-icons left'>search</i>search</button>
                    
                     </div>
                     </div>
                </div>
                <div className='side-filter-con'>
                 <h6 className='sf-head'><i className="material-icons">filter_list</i>Filter</h6>   
                 <blockquote className='sf-li-head'>Tenant Type</blockquote>
                 <div className='container'>
                    <button className='filbtn'>male</button>
                    <button className='filbtn'>female</button>
                    <button className='filbtn'>any</button>
                 </div>
                 <blockquote className='sf-li-head'>Furnishing</blockquote>
                 <div className='container'>
                    <button className='filbtn'>Full</button>
                    <button className='filbtn'>Semi</button>
                    <button className='filbtn'>non</button>
                 </div>
                 <blockquote className='sf-li-head'>Bedrooms</blockquote>
                 <div className='container'>
                    <button className='filbtn'>Full</button>
                    <button className='filbtn'>Semi</button>
                    <button className='filbtn'>non</button>
                    <button className='filbtn'>Full</button>
                    <button className='filbtn'>Semi</button>
                    <button className='filbtn'>non</button>
                 </div>
                 <blockquote className='sf-li-head'>Furnishing</blockquote>
                 <div className='container'>
                    <button className='filbtn'>Full</button>
                    <button className='filbtn'>Semi</button>
                    <button className='filbtn'>non</button>
                 </div>
                </div>
            </div>
        </div>
    )
}