import React, { Component } from 'react';


export default function propbox(prop){
    const img = prop.image
    const name = prop.name
    const locality = prop.locality
    const city = prop.city
    const like = prop.like
    const rent = prop.rent
    const acctype = prop.acctype
    const deposit = prop.deposit
    const area  = prop.area
    const construction = prop.construction
    const gender = prop.gender
    const description = prop.description
    const book = prop.book
    const detail = prop.detail
    const visit = prop.visit

    return(
        <div className='mode-container'>
                                <div className='row-container'>
                                <div className='col s12 m6 no-pad deep-mode mc-left'>
                                    <img src={img} className="image-mode"/>
                                    
                            
                                    <div className='over-image-section'>
                                        <a className='name-title'>{name}</a>
                                        <a className='loca-label'>{locality},{city}</a>
                                    </div>

                                </div>
                                <div className='col s12 m6 mc-right'>
                                    <div className=''>
                                        <div className='col s12 m12 no-pad'>
                                        <div className='heart'>
                                       <button className='btn btn-floating hearticon red right' onClick={()=>{alert("liked")}}><i className='material-icons'>favorite_border</i></button> 
                                    </div>
                                            <div className='price-container'>

                                            <button className='btn btn-flat large black btn-large white-text right right-btn-mode z-depth-5'>Book</button>
                                                <div className='valign-wrapper'>
                                                    <h6 className='pc-header grey lighten-3 grey-text text-darken-1'>Price</h6>                                                   
                                                </div>
                                                <div className='pc-rent'> 
                                                <a className='black-text'>Rs.{rent}<span className='badge orange lighten-4 black-text pc-rent-badge'>{acctype}</span></a><br></br>
                                                <a class="black-text text-lighten-3">Rs.{deposit}<span className='badge teal lighten-4 black-text pc-rent-badge'>Deposit</span></a>
                        
                                                </div>
                                            </div>
                                        </div>
                                        <div className='col s12 m12 f-con'>
                                        <div className='row pcpmode-con'>
                                            <div className="col s12 m12">
                                            <div className='col s3 m3 center propcol'>
                                                <div className='propcardpanel-mode propcol'>
                                            <i className='material-icons deep-purple-text circle amber icon-mode'>map</i>
                                            
                                                <h6 className='no-pad-p-mode'> {area}</h6>
                                                <a className='label-mode'>sqft</a>
                                            </div>
                                            </div>
                                            <div className='col s3 m3 center propcol'>
                                            <div className='propcardpanel-mode'>
                                            <i className='material-icons  deep-purple-text circle yellow icon-mode'>insert_invitation</i>
                                            
                                                <h6 className='no-pad-p-mode'> {construction}</h6>
                                                <a className='label-mode'>Age</a>
                                            </div>
                                            </div>
                                            <div className='col s3 m3 center propcol'>
                                            <div className='propcardpanel-mode'>
                                            <i className='material-icons  deep-purple-text circle lime icon-mode'>weekend</i>
                                            <h6 className='no-pad-p-mode'>full</h6>
                                            <a className='label-mode'>Furnished</a>
                                               </div>
                                               </div>
                                            <div className='col s3 m3 center propcol'>
                                            <div className='propcardpanel-mode'>
                                            <i className='material-icons deep-purple-text circle orange icon-mode'>perm_identity</i> 
                                            <h6 className='no-pad-p-mode'>{gender==="any"?"Any":gender}</h6>
                                            <a className='label-mode'>Gender</a>
                                            </div>
                                            </div>
                                            </div>
                                        </div>
                                        </div>
                                        <div className='col s12 m12 no-pad desc'>
                                            <p className='desctext-mode'>
                                            Lorem ipsum dolor sit amet. Vel voluptatibus debitis qui sequi velit qui quidem earum rem porro dolorem eos voluptatum enim est dolor minima. 
                                                <span className='rmore'>read more</span>
                                            </p>
                                        </div>
                                        <div className="col s12 m12">
                                            <div className='mode-footer'>
                                                
                                                <div className='col s12 m6'>
                                                <button className='btn btn-flat deep-purple lighten-3 white-text right mofo-btn'
                                                onClick={detail}>
                                                    view details</button>
                                                </div>
                                                <div className='col s12 m6'>
                                                <button className='btn btn-flat large black white-text mofo-btn'>
                                                    schedule visit</button>
                                                </div>
                                            </div>
                                        </div>
                                        
                                       
                                        

                                    </div>
                                </div>
                                </div>
                            </div>
    )
}