import React, { Component } from 'react';
import { useEffect, useRef, useState } from "react";
import 'materialize-css/dist/css/materialize.min.css';
import M from 'materialize-css/dist/js/materialize.min.js';





export default function UploadPics(props){
    useEffect(()=>{
        var elems = document.querySelectorAll('.modal');
        M.Modal.init(elems,{});
         var ele = document.querySelectorAll('.timepicker');
        M.Timepicker.init(ele, {});
        var elem = document.querySelectorAll('.datepicker');
        M.Datepicker.init(elem, {});
        var el = document.querySelector('.tabs')
        M.Tabs.init(el,{});
        var el = document.querySelectorAll('.collapsible');
        M.Collapsible.init(el,{});
    },[])
    const images = [
        "https://images.unsplash.com/photo-1549989476-69a92fa57c36?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=800&q=60",
        "https://images.unsplash.com/photo-1549396535-c11d5c55b9df?ixlib=rb-1.2.1&auto=format&fit=crop&w=800&q=60",
        "https://images.unsplash.com/photo-1550133730-695473e544be?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=800&q=60",
        "https://images.unsplash.com/photo-1550167164-1b67c2be3973?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=800&q=60",
        "https://images.unsplash.com/photo-1550338861-b7cfeaf8ffd8?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=800&q=60",
        "https://images.unsplash.com/photo-1550223640-23097fc71cb2?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=800&q=60",
        "https://images.unsplash.com/photo-1550353175-a3611868086b?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=800&q=60",
        "https://images.unsplash.com/photo-1550330039-a54e15ed9d33?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=800&q=60",
        "https://images.unsplash.com/photo-1549737328-8b9f3252b927?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=800&q=60",
        "https://images.unsplash.com/photo-1549833284-6a7df91c1f65?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=800&q=60",
        "https://images.unsplash.com/photo-1549985908-597a09ef0a7c?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=800&q=60",
        "https://images.unsplash.com/photo-1550064824-8f993041ffd3?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=800&q=60"
      ];
    return(<div>
        <div className='section'>
            <div className='container'>
            <div className='adp_header_con'>
                    <h6 className='adp_header'>Add Property Photos</h6>
                </div>
            <button className='btn btn-flat black white-text btn-large'>
                <i className='material-icons left'>add</i>
                Upload Pics</button>
            <div className='row'>
                {images.map((image)=>{return(<div>
                    <div className='uploadedImages col s3 m2'>
                    <img src={image} className='uploadedimage-item'/>
                    <i className='material-icons'>delete</i>
                </div> </div>)})}
                </div>
           
            
            <div className='adp_header_con'>
                    <h6 className='adp_header'>Schedule Inspection</h6>
                    <div className='valign-wrapper'>
                                        <div className='dt-con'>
                                            <div className='serve-date valign-wrapper'>
                                                <i className='material-icons left'>date_range</i>
                                        <input type="text" className='datepicker' placeholder='Pick Date'/>
                                        </div>
                                        </div>
                                        <div className='dt-con'>
                                            <div className='serve-date valign-wrapper'>
                                                <i className='material-icons left'>schedule</i>
                                        <input type="text" className='timepicker' placeholder='Pick Time'/>
                                        </div>
                                    </div>
                                    </div>
                </div>
                
                
                <div className='col s12 m12 '>
                <div className='adp_label_con'>
                    <a className='btn btn-flat btn-large black white-text'>Save and Proceed</a>
                </div>
                </div>
                
                </div>
        </div>
    </div>

    )
}