import React, { Component } from 'react';
import { useLocation } from 'react-router-dom';
import Logo from '../components/logo'
import Data from '../data/view';
import M from 'materialize-css/dist/js/materialize.min.js';
import 'materialize-css/dist/css/materialize.min.css';
import { useEffect, useRef, useState } from "react";
import Simplar from './viewsimilar';
import {useHistory} from 'react-router-dom';







export default function Confirm(){
    const navigate = useHistory();
    const returnHome=()=>{
        navigate.push({
            pathname:"/",
    })
}
    return (
        <div className=''>
            <div>
            <div className='top_thin_bar'>
                    <div className='row view_row'>
                    <div className='col s12 m12 col-blue white'>
                        <Logo/>
                        </div>
                    </div>    
                </div>
                <div className='section_second_confirm container center'>
                    <div className='card-panel confirm-panel'>
                        <div className='card-content'>
                            <h6 className='conf-head'>Your Request has been recieved.</h6>
                            <h6 className='conf-head'>Your property will be live in 24 hrs</h6>
                            <br></br>
                            <button className='btn btn-flat large black white-text lighten-3' onClick={()=>{returnHome()}}>Home</button>
                        </div>
                    </div>
                </div>
            </div>

        </div>
    )
}