import React, { Component } from 'react';
import Logo5 from '../assests/img/logo5.png';
import { useEffect, useRef, useState } from "react";

export default function Logo(props){
    const pos = props.pos;
    const [textc,sett] = useState()
    useEffect(()=>{
        if(pos==="footer"){
            sett("white-text")
        }else{
            sett("black-text")
        }
    },[])

    return (
        <div className='logost'>
            <div className='valign-wrapper '>
                <div className='logoimg-con'><img src={Logo5} className="logoiconimg"/></div>
                <a className={`logoimg ${textc} hide-on-small-only`}>Rentobro<span></span></a>
            </div>
        </div>
    )
}