import React, { Component } from 'react';
import Nav from './lognav';

export default function PaymentPage(props){
    return(<div>
        <Nav/>
        <div className='section bg-color'>
            <div className='container pay-con'>
                <div className='row'>
                <div className='pay-panel-main col s6 m6'>
                    <a className='detail_head black-text'>Your Account</a>
                    <a className='p-s-b-head black-text'>Amit 12345677899</a>
                </div>
                <div className='pay-panel-main col s6 m6'>
                    <a className='detail_head black-text'>Add Amount</a>
                    <a className='p-s-b-head black-text'>Rs.10,000/-</a>
                    <a className='p-s-b-head black-text'>Rent</a>
                </div>
                </div>
                <div className=' col s12 m12 row'>
                <div className='pay-panel-main col s12 m6'>
                    <a className='detail_head black-text'>Add Fund</a>
                    <div className='payput-con'>
                    <input type="text" placeholder='Rs.0.00' className='payput'/>
                    </div>
                    </div>
                    <div className=' col s12 m6'>
                    <button className='btn btn-flat black white-text  btn-pay'>Pay Now</button>
                    </div>
                    </div>
                </div>
            </div>
        </div>

    )
}