import React, { Component } from 'react';
import { useHistory, useLocation } from 'react-router-dom';
import Logo from '../components/logo'
import Data from '../data/view';
import M from 'materialize-css/dist/js/materialize.min.js';
import 'materialize-css/dist/css/materialize.min.css';
import { useEffect, useRef, useState } from "react";
import Simplar from './viewsimilar';
import Simplcar from './viewcar';
import Simplvid from './viewvide';
import Book from '../components/book';

export default function Visit(){

    let curr = new Date 
    let weekday_arr = []
    let year_arr = []
    let month_arr = []

        for (let i = 1; i <= 7; i++) {
        let first = curr.getDate() - curr.getDay() + i 
        let day = new Date(curr.setDate(first)).toISOString().slice(0, 10)
        let formatted = day.split("-")
        let year = formatted[0]
        let month = formatted[1]
        let weekday = formatted[2]
        weekday_arr.push(weekday)
}

const [date,setDate] = useState(new Date().toLocaleString()+"")
const [time,setTime] = useState("")
const formatted = date.split("/")
const day = parseInt(formatted[1])
const month = formatted[0]
const token ="1999";

    console.log(day)
    const [seldate,setseldate] = useState();
    const [seltime,setseltime] = useState();
    const [datecss,setdatecss] = useState("day_con");
    const [datecss1,setdatecss1] = useState("day_con");
    const [datecss2,setdatecss2] = useState("day_con");
    const [datecss3,setdatecss3] = useState("day_con");
    const navigate = useHistory();
    const handleday = (day) =>{
        
        if(day===0){
            setseldate(day)
             setdatecss("day_con_next") 
             setdatecss1("day_con") 
            setdatecss2("day_con")
            setdatecss3("day_con") 
        }if(day===1){
            setseldate(day)
            setdatecss("day_con")
            setdatecss1("day_con_next") 
            setdatecss2("day_con")
            setdatecss3("day_con") 
        }
        if(day===2){
            setseldate(day)
            setdatecss("day_con") 
            setdatecss1("day_con") 
            setdatecss2("day_con_next")
            setdatecss3("day_con")  
        }
        if(day===3){
            setseldate(day)
            setdatecss1("day_con") 
            setdatecss2("day_con")
            setdatecss("day_con") 
            setdatecss3("day_con_next") 
        }
        M.toast({html: 'Please select a Time',classes:'rounded'})
    }
    const handletime = (time) =>{
        
        setseltime(time)
        navigate.push({
            pathname:"/logvisit",
            day:seldate,
            time:seltime
        })
    }




    return (
        <div>
            <div className='visit-con center'>
                                            <h6 className='black-text date_con'>Schedule Your Visit</h6>
                                        
                                            <h6 className='date_con black-text'>Pick a Date</h6>
                                            
                                            <div className='valign-wrapper'>
                                                <div className={datecss}>
                                                <a onClick={()=>{handleday(0)}} className='day orange lighten-3 circle'>{day}</a>
                                                <a className='month black-text'>March</a>
                                                </div>
                                                
                                                <div className={datecss1}>
                                                <a onClick={()=>{handleday(1)}} className='day teal lighten-3 circle'>{day+1}</a>
                                                <a className='month black-text'>March</a>
                                                </div>

                                                <div className={datecss2}>
                                                <a onClick={()=>{handleday(2)}} className='day indigo lighten-3 circle'>{day+2}</a>
                                                <a className='month black-text'>March</a>
                                                </div>
                                                <div className={datecss3}>
                                                <a onClick={()=>{handleday(3)}} className='day green lighten-3 circle'>{day+3}</a>
                                                <a className='month black-text'>March</a>
                                                </div>

                                            </div>
                                            <h6 className='date_con black-text'>Pick a Time slot</h6>
                                            <a onClick={()=>{handletime('1-2pm')}} className='btn btn-flat blue-grey lighten-5 timebtn black-text'>1-2pm</a>
                                            <a onClick={()=>{handletime('2-4pm')}} className='btn btn-flat blue-grey lighten-5 timebtn black-text'>2-4pm</a>
                                            <a onClick={()=>{handletime('4-6pm')}} className='btn btn-flat blue-grey lighten-5 timebtn black-text'>4-6pm</a>

                                        </div>
            </div>
        
    )
}