import React, { Component } from 'react';
import Slide from '../assests/img/slide.jpeg'; 

import { useEffect, useRef, useState } from "react";
import 'materialize-css/dist/css/materialize.min.css';
import M from 'materialize-css/dist/js/materialize.min.js';



export default function Service(prop){

    useEffect(()=>{
        var elems = document.querySelectorAll('.modal');
        M.Modal.init(elems,{});
         var ele = document.querySelectorAll('.timepicker');
        M.Timepicker.init(ele, {});
        var elem = document.querySelectorAll('.datepicker');
        M.Datepicker.init(elem, {});
        var el = document.querySelector('.tabs')
        M.Tabs.init(el,{});
        var el = document.querySelectorAll('.collapsible');
        M.Collapsible.init(el,{});
    },[])
    
    const electrical = ['Fan repair','Bulb repair','Ac repair']
    var [cart,setCart] = useState([])
    const handleCart = (item) => {
        setCart(cart=>[...cart,item])
        M.toast({html: item+' '+'service'+' added',classes:'rounded'})
    }
    const handleCartClose = (item) => {
        let value = item
        setCart((cart)=>cart.filter(function(item){
            return item != value
        }))
        M.toast({html: item+' '+'service'+' removed',classes:'rounded'})
    }

    const active_service = []






    return (
        <div>
            <div className='card-panel cart-panel hide-on-large-only '>
                <div className='card-content modal-trigger' data-target="modal1">
                    <h6 className='p-s-b-head black-text' ><i className='material-icons left'>room_service</i>
                    Services Added (2)</h6>
                </div>
            </div>
            <div id="modal1" class="modal cart-modal bottom-sheet">
           
           <div class="modal-content">
            <div className='container'>
               <i className='material-icons small right modal-close' >close</i>
           </div>
            <h6 className='p-s-b-head'>Your Added Services</h6> 
            {cart.map((item)=>{
                                        return(
                                            <div className='list-service'>
                                            <i className='material-icons right' onClick={()=>{handleCartClose(item)}}>close</i>
                                               <a className='left black-text'> {item}</a>
                                             </div>
                                        )
                                    })} 
                       <br></br>
                        <div className='modal-action serve-action'>
                                    <div className='valign-wrapper'>
                                        <div className='dt-con'>
                                            <div className='serve-date valign-wrapper'>
                                                <i className='material-icons left'>date_range</i>
                                        <input type="text" className='datepicker' placeholder='Pick Date'/>
                                        </div>
                                        </div>
                                        <div className='dt-con'>
                                            <div className='serve-date valign-wrapper'>
                                                <i className='material-icons left'>schedule</i>
                                        <input type="text" className='timepicker' placeholder='Pick Time'/>
                                        </div>
                                    </div>
                                    </div>
                                    </div>

                                    <div className='center'>
                                        <button className='btn btn-flat black white-text'>Book Service</button>
                                    </div>

       </div>
      </div>
            <br></br>
            <div class="row autabs">
                <div class="col s12">
                <ul class="tabs">
                    <li class="tab col s3"><a class="p-s-b-head black-text autab" href="#test1">
                        <i className='material-icons left green-text text-accent-4'>style</i>
                        Book Services</a></li>
                    <li class="tab col s3"><a class="p-s-b-head black-text autab" href="#test2">
                    <i className='material-icons left amber-text'>stars</i>
                        Active Services</a></li>
                </ul>
                </div>
                <div id="test1" class="col s12">
                    <div className='row'>
                        <div  className='col s12 m8'>
                        <ul class="collapsible serv-coll grey lighten-5">
                            <li>
                            <div class="collapsible-header"><i class="material-icons lime-text text-lighten-1">arrow_drop_down_circle</i>Electrical</div>
                            <div class="collapsible-body">
                                {electrical.map((item)=>{
                                    return(
                                        <div className='btn serve-btn' onClick={()=>handleCart(item)}>
                                            {item}
                                            <i className='material-icons left'>add</i>
                                        </div>
                                    )
                                })}
                            </div>
                            </li>
                            <li>
                            <div class="collapsible-header"><i class="material-icons lime-text text-lighten-1">arrow_drop_down_circle</i>Plumber</div>
                            <div class="collapsible-body"><span>Lorem ipsum dolor sit amet.</span></div>
                            </li>
                            <li>
                            <div class="collapsible-header"><i class="material-icons lime-text text-lighten-1">arrow_drop_down_circle</i>Doctor</div>
                            <div class="collapsible-body"><span>Lorem ipsum dolor sit amet.</span></div>
                            </li>
                        </ul>
                        </div>
                        <div className='col s6 m4 right'>
                            <div className='card service-cart blue-grey lighten-5 hide-on-small-only'>
                                <div className='card-content'>
                                    <div className='sc-top'>
                                        <h6 className='p-s-b-head'>Your Services</h6>
                    
                                    </div>
                                    <div className='divider'></div>

                                    {cart.map((item)=>{
                                        return(
                                            <div className='list-service'>
                                            <i className='material-icons right' onClick={()=>{handleCartClose(item)}}>close</i>
                                               <a className='left black-text'> {item}</a>
                                             </div>
                                        )
                                    })}
                                </div>
                                <div className='card-action serve-action'>
                                    <div className='valign-wrapper'>
                                        <div className='dt-con'>
                                            <div className='serve-date valign-wrapper'>
                                                <i className='material-icons left'>date_range</i>
                                        <input type="text" className='datepicker' placeholder='Pick Date'/>
                                        </div>
                                        </div>
                                        <div className='dt-con'>
                                            <div className='serve-date valign-wrapper'>
                                                <i className='material-icons left'>schedule</i>
                                        <input type="text" className='timepicker' placeholder='Pick Time'/>
                                        </div>
                                    </div>
                                    </div>
                                    </div>
                                <div className='card-action'>
                                        <button className='btn btn-flat black white-text'>Book Service</button>
                                    </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div id="test2" class="col s12">
                    <div className='card-panel active-serve lime lighten-4'>
                        <div className='card-content'>
                            <div className='row'>
                                <div className='col s12 m1'>
                                    <a className='chip amber'>01</a>
                                </div>
                                <div className='col s12 m5'>
                                <a className='p-s-b-label black-text'>Your Services</a>
                                    {cart.map((i)=>{return(<a className='p-s-b-head-2 black-text chip transparent'> {i} </a>)})}
                                </div>
                                <div className='col s6 m3'>
                                    <a className='p-s-b-label black-text'>Date Of Services</a>
                                    <a className='p-s-b-head black-text'>12-03-2023</a>
                                    <a className='p-s-b-label black-text'>Status</a>
                                    <a className='p-s-b-head black-text'>Pending</a>
                                </div>
                                <div className='col s6 m3'>
                                    <a className='p-s-b-label black-text'>Action</a>
                                    <button className='btn btn-flat white-text black'>
                                        <i className='material-icons left'>close</i>
                                        cancel</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}