import React, { Component } from 'react';
import Blank from '../components/blank';
import MediaLogoCon from '../components/MediaLogoCon';

export default function MediaLogo(props)
{
    const src= props.src;
    return(
        <div>
    {src ==="na"?<Blank/>:<MediaLogoCon src={src}/>}
    </div>
    )
}