import React, { Component } from 'react';
import Search from '../pages/sidesearch'
import {useHistory} from 'react-router-dom';
import 'animate.css'
export default function Modsearch(){
    const navi = useHistory();
    const handleClose=()=>{
        navi.push({
            pathname:"/list"
        })
    }
    return (
        <div className='section white '>
           
            <div className='container'>
            <br></br>
            <h6 className='filterhead'>Search Properties</h6>
            </div>
            <br></br>
            <div className='divider'></div>
            <div className='valign-wrapper'>
                            <select className='selectbar-mod'>
                                <option value="">Select City</option>
                                <option value="patna">Patna</option>
                                <option value="jabalpur">Jabalpur</option>
                            </select>
                            <select className='selectbar-mod right'>
                                <option value="patna">Rent</option>
                                <option value="jabalpur">Buy</option>
                            </select>
                            </div>
                            <div className='searchbar-mod'>
                            <input type="text" id="search" className='searchbar-filter' placeholder="Search locality, Area, Pincode..." />
            
                            </div>
                            <div className='center btn-filter-con'>
                                <button className='btn btn-flat btn-filter-mod black white-text'>Search</button>
                            </div>
            <br></br>
            
        </div>
    )
}