import React, { Component } from 'react';
import { useEffect, useRef, useState } from "react";
import axios from "axios";
import 'materialize-css/dist/css/materialize.min.css';
import M from 'materialize-css/dist/js/materialize.min.js';
import {useHistory} from "react-router-dom";


export default function Search(){
 // fetching data
 
 const [place,setplace] = useState([]);
 const fetchPlace = async() =>{
    const response  = await axios.get("http://aahaarsetu.com/locality.php?city=patna");
    setplace(response.data)
 }
useEffect(()=>{
    fetchPlace();
},[]);

//fetch search

const [filtered,setFiltered] = useState([]);
const [search,setSearch] = useState("");
const [res,setRes] = useState("no-result");
const [value,setValue] = useState("");
const [city,setCity] = useState("");
const navigate = useHistory();
const [act,setAct] = useState("buy active");

const searchRef = useRef();
const updateHit = () =>{
    if(value){
        const dataform = new FormData();
        dataform.append('name',{value})
        const update = async()=>{await axios.post("http://aahaarsetu.com/updatelocality.php",{
            name:{value},
        })}
        console.log(value)
    }
}
const onSearch = () =>{
    console.log(value)
    console.log(city)
    console.log(act)
    if(value.length === 0){
        M.toast({html: 'Please select a locality',classes:'rounded'})
    }
    if(city.length === 0){
        M.toast({html: 'Please select a city',classes:'rounded'})
    }
    if(value != "" && city != "" && act !=""){
        navigate.push({
            pathname:"/list",
            locality:value,
            city:city,
            activity:act
        })
    }
}

useEffect(()=>{
    if(search.length>0){
        setRes("result")
    }else{
        setRes("no-result")
    }
    setFiltered(place.filter((item)=>item.name.toString().toLowerCase().startsWith(search.toLowerCase())))
},[search]);




return (
<div className='row searchcon '>
    <div className='valign-wrapper rent-btn-con  '>
    <a  onClick={()=>setAct("rent btnactive")} className={(act=="rent btnactive")?'btn btn-flat   btnactive':'btn btn-flat   black-text'}>Rent</a>
    <a  onClick={()=>setAct("buy btnactive")} className={(act=="buy btnactive")?'btn btn-flat  btnactive':'btn btn-flat   black-text'}>Buy</a>
            
       
    </div>
        <div className='valign-wrapper search-z '>
             <div className='card-panel search-panel'>
            <div className='col s12 m3 select-con '>
                <div className='head-label'>
                    <h6 className='head-label-text'>
                    <i className='material-icons left deep-purple-text'>location_on</i>
                    City</h6>
                </div>
                <div className=''>
            <select className='selectbar' onChange={(e)=>setCity(e.target.value)}>
                <option value="">Select City</option>
                <option value="patna">Patna</option>
                <option value="jabalpur">Jabalpur</option>
            </select>
            </div>
            </div>
            <div className='col s12 m7 search-con-f'>
            <div className='head-label'>
                    <h6 className='head-label-text'>
                        <i className='material-icons left deep-purple-text'>location_on</i>
                        Locality</h6>
                </div>
            <input type="text" id="search" className='searchbar' placeholder="Search locality, Area, Pincode..."   onChange={(e)=>setSearch(e.target.value)} ref={searchRef}/>
            
            </div>
            <div className='col s12 m2' style={{paddingRight:"0px"}}>
            <button type="submit" onClick={onSearch} className='btn btn-flat black white-text searchbtnf' id="submit">
                <i className='material-icons search-icon left  circle search-iconf'>search</i>
               <a className='hide-on-large-only white-text'>search</a> </button>
            </div>
            </div>
            
        </div>
       
        <div className='left-align'>
            <div className={res}>
                <div className='card-panel result-panel '>
                <div className='collection no-border'>
                {filtered.length>0?filtered.slice(0,5).map((item,index)=>{
                    return (
                        <a className='collection-item locality black-text' key={index} onClick={(e)=>{
                            searchRef.current.value = item.name;
                            setValue(searchRef.current.value)
                            setSearch("");
                            setRes("no-result")
                        }}>
                            <i className='material-icons left purple-text text-darken-4'>location_on</i>
                            {item.name}
                            <a className='right black-text'><span className='lime black-text badge left'>1404</span>&nbsp;new</a>
                            </a>
                    )
                }):<a className=''>
                    <i className="material-icons left black-text">youtube_searched_for</i>
                    <p className='black-text'>no result</p></a>}
                </div>
               
                </div>
                </div>
                
            </div>
        </div>
    )





}