import React, { Component } from 'react';

export default function Err(props){
    const error = "error 404"
    return(
        <div>
            <div className='section white'>
                <div className='container'>
                    <div className='aboutCon center'>
                
                        <h6 className='error-head'>Oops!</h6>
                        <h6 className='error-head-2'>Page Not Found</h6>
                        <div className='card-panel err-panel'>
                            <div className='card-content'>
                                <h6 className='ca-head err-head'>{error}</h6>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}