import React, { Component } from 'react';
import Puspha from '../assests/img/pushpa.jpg';

export default function Mads(props){
    return(
        <div>
            <div className='ad-title'>
                <a className='p-s-b-label black-text'>Sponsored</a>
            </div>
        <div className='card-panel mads'>
            <div className='card-content center'>
                <img src={Puspha} className='img-mads'/>
                <a className='ad-title-text'>
                    Jhukega nhi sala
                </a>
                <div className='card-action ad-action'>
                <button className='btn btn-flat black white-text'>Click</button>
                </div>
            </div>
        </div>
        </div>
    )
}