import React, { Component } from 'react';

export default function Block(props){
    const data = props.options
    const onChange = props.onChange
    console.log(data)
    return (
        <div className='row'>
            {data.map((item,i)=><div className='col s12 m3'>
                <div onClick={onChange} className='card-panel block '>
                    <i className='material-icons left circle grey grey-text text-lighten-3'>check</i>
                <a  className='block_content'>{item}</a>
                </div></div>)}
        </div>
    )
}