import React, { Component, useState } from 'react';

export default function Hormenu(props){
    const onClick = props.onClick
    console.log(onClick)
    return(
        <div>
            <div className='card-panel hornav-panel '>
                <div className='valign-wrapper'>

                    <div className='hornav-li'>
                    <a className='hornav-label' onClick={()=>onClick("home")}>Properties</a>
                    </div>
                    
                    <div className='hornav-li'>
                            <a className='hornav-label' onClick={()=>onClick("money")}>Visits</a>
                        </div>
                        <div className='hornav-li'>
                            <a className='hornav-label' onClick={()=>onClick("plus")}>Services</a>
                        </div>
                    <div className='hornav-li'>
                            <a className='hornav-label' onClick={()=>onClick("people")}>Tenants</a>
                        </div>
                   
                    <div className='hornav-li'>
                            <a className='hornav-label' onClick={()=>onClick("property")}>Accounts</a>
                        </div>
                        <div className='hornav-li'>
                            <a className='hornav-label' onClick={()=>onClick("property")}>Help</a>
                        </div>
                        
                </div>
            </div>
           </div>
    )
}