import React, { Component } from 'react';
import Slide from '../assests/img/slide.jpeg'; 

import { useEffect, useRef, useState } from "react";
import 'materialize-css/dist/css/materialize.min.css';
import M from 'materialize-css/dist/js/materialize.min.js';

















export default function Visits(props){
    const v_data = [['121','Priyanka Chopra','12-3-2023','10,000','visit',['Bachelor',' Engineer',' Working Professional']],['121','Nick Jonas','12-3-2023','10,000','visit',['Bachelor']]]
   const handleConfirm = (e) =>{
    M.toast({html: 'Confirm Clicked',classes:'rounded'})
   }
   const handleReject = (e) =>{
    M.toast({html: 'Reject Clicked',classes:'rounded'})
   }
   const [sch,setSch] = useState("hide")
   const handleSchedule=()=>{
        setSch("show")
   }
useEffect(()=>{
    var elems = document.querySelectorAll('.modal');
     M.Modal.init(elems,{});
     var ele = document.querySelectorAll('.timepicker');
    M.Timepicker.init(ele, {});
     var elem = document.querySelectorAll('.datepicker');
      M.Datepicker.init(elem, {});
},[])
   
    return (
        
        <div className='section'>
            <div id="modal1" class="modal bottom-sheet date-time-modal">
                <div class="modal-content container">
                
                
                </div>
                
            </div>
            {v_data.map((item)=>{
                return(
                    
                     <div className='auvisit-con'>
                        <div className='white auvisit row'>
                        <div className='col s7 m3 '>
                        <img src={Slide} className='pdc-img-mid'/>  
                        </div>
                            <div className='col s4 m3'>
                                <ul className='collection no-border'>
                                    <li>
                                    <a className='name-label grey-text'>Property Detail</a>
                                    <a className='black-text p-s-b-head'>Name of {item.at(0)}</a>
                                    <br></br>
                                    <a className='name-label grey-text'>Rent</a>
                                    <a className='black-text p-s-b-head'>Rs.{item.at(3)}/-</a>
                                    
                                    </li>
                                </ul>
                            </div>
                            <div className='col s8 m4'>
                                <ul className='collection no-border'>
                                    <li>
                                        <a className='name-label grey-text'>booked by</a>
                                        <a className='tenant-name p-s-b-head black-text'>{item.at(1)}</a>
                                        <br></br>
                                        <a className='name-label grey-text'>Applicant Profile</a>
                                        {item[5].map((x)=>{return(<a className='p-s-b-head-2 white black-text'>{x}</a>)})}
                                        
                                    </li>
                                </ul>
                            </div>
                            <div className='col s4 m2'>
                            <ul className='collection no-border'>
                                    <li>
                                        <a className='name-label grey-text'>Booking Type</a>
                                        <a className='tenant-name p-s-b-head black-text'>{item.at(4)}</a>
                                        <br></br>
                                        <a className='name-label grey-text'>Date Of Visit/Time</a>
                                        <a className='tenant-name p-s-b-head black-text'>{item.at(2)}<span className='time-span'>10:00 am</span></a>
                                        
                                    </li>
                                </ul>
                            </div>
                            <br></br>
                            <div className='col s12 m12 white'>
                                    <a className='p-s-b-label black-text'>Action</a>
                                    <div className='action-con white'>
                                    <button className='btn btn-flat btn-pad small green accent-2 black-text' onClick={handleConfirm}>
                                    <i className='material-icons left'>check</i>
                                    Confirm</button>
                                    <button className='btn btn-flat small btn-pad green accent-2 black-text' onClick={handleReject}>
                                    <i className='material-icons left'>close</i>
                                    Reject</button>
                                    <button className='btn btn-flat small btn-pad green accent-2 black-text datepicker' onClick={handleSchedule}>
                                    <i className='material-icons left'>date_range</i>
                                    Change Date</button>
                                    <button className='btn btn-flat small btn-pad green accent-2 black-text timepicker'>
                                    <i className='material-icons left'>schedule</i>
                                    Change Time</button>
                                        </div>   
                                </div>
                     </div>
                     </div> 
                        
                   
                )
            })}
             
        </div>
    )
}