import React, { Component } from 'react';
import { useHistory } from 'react-router-dom/cjs/react-router-dom.min';


export default function Book(){
    return (
        <div>
            <div className='container'>
                
                <div className='book-con center'>
                
                    <h6 className='book_name'>Pay Token Money</h6>
                
                <div className='price-con center'>
                <h6 className='book_price'>Rs.2000 /-</h6>
                <a className='price-label'>(Inclusive all taxes.)</a>
                </div>
                <div className='btn-container center'>
                    <button className='btn  btn-large black z-depth-5 white-text pay-btn'>Pay</button>
                    <br></br>
                   
                </div>
                <button className='btn btn-large  black-text back-btn' >Back</button>
            </div>
            </div>
        </div>
    )
}