import React, { Component } from 'react';


export default function LoginV(){
    return (
        <div>
            <div className='center'>
            <div className='top_thin_bar'>
                    <div className='row view_row'>
                        <div className='col s12 m12 col-blue white'>
                            <i className='material-icons' onClick={()=>{alert("login")}}>Close</i>
                        </div>
                    </div>    
            </div>
            <div className='second_section_conf login_con'>
                <div className='container'>
                    <h6 className='login_header'>Login</h6>
                <div className='login_form'>
                    <div className='section'>
                    <h6 className='head-label-text'>Number</h6>
                    </div>
                    <div className='login_num'>
                        <input className='login_no' placeholder='Enter Your Number'/>
                    </div>
                    <div>
                        <button className='btn btn-flat small black white-text'>Get OTP</button>
                    </div>
                    <div>
                    <h6 className='head-label-text'>OTP</h6>
                    </div>
                    <div className='login_otp'>
                    <input className='login_no' placeholder='Enter Your Number'/>
                    </div>
                    <div className=''>
                        <button  className='btn btn-flat btn-large black white-text sub-login'>Submit</button>
                    </div>
                </div>
                </div>
            </div>
        </div>
        </div>
    )
}