import React, { Component } from 'react';

export default function BlogBox(props){
    const body = props.body;
    const src  = props.src;
    const title = props.title;

    return(
        <div>
            <div className='card  blog-c'>
                  <div className='card-image blog-ci'>
                 <img className='blog-c-img' src={src}/>
                 </div>
                <div className='card-content'>
                <h6 className='blog-c-text'>{title}</h6>
                <p className='blog-c-p-text'>{body}</p>
                </div>
            </div>
        </div>
    )
}