import React, { Component } from 'react';
import { useEffect, useRef, useState } from "react";
import axios from "axios";
import 'materialize-css/dist/css/materialize.min.css';
import M from 'materialize-css/dist/js/materialize.min.js';
import {useHistory} from "react-router-dom";


export default function Sidesearch(){
 // fetching data
 
 const [place,setplace] = useState([]);
 const fetchPlace = async() =>{
    const response  = await axios.get("http://aahaarsetu.com/locality.php?city=patna");
    setplace(response.data)
 }
useEffect(()=>{
    fetchPlace();
},[]);

//fetch search

const [filtered,setFiltered] = useState([]);
const [search,setSearch] = useState("");
const [res,setRes] = useState("no-result");
const [value,setValue] = useState("");
const [city,setCity] = useState("");
const navigate = useHistory();
const [act,setAct] = useState("buy active");

const searchRef = useRef();
const updateHit = () =>{
    if(value){
        const dataform = new FormData();
        dataform.append('name',{value})
        const update = async()=>{await axios.post("http://aahaarsetu.com/updatelocality.php",{
            name:{value},
        })}
        console.log(value)
    }
}
const onSearch = () =>{
    console.log(value)
    console.log(city)
    console.log(act)
    if(value.length === 0){
        M.toast({html: 'Please select a locality',classes:'rounded'})
    }
    if(city.length === 0){
        M.toast({html: 'Please select a city',classes:'rounded'})
    }
    if(value != "" && city != "" && act !=""){
        navigate.push({
            pathname:"/list",
            locality:value,
            city:city,
            activity:act
        })
    }
}

useEffect(()=>{
    if(search.length>0){
        setRes("side-result")
    }else{
        setRes("no-result")
    }
    setFiltered(place.filter((item)=>item.name.toString().toLowerCase().startsWith(search.toLowerCase())))
},[search]);


return (
<div className='row side-search-row'>
    
        <div className='card side-search-panel'>
        <div className='valign-wrapper'>
    <a  onClick={()=>setAct("rent btnactive")} className={(act=="rent btnactive")?'btn btn-flat  side-btnactive':'btn btn-flat  black-text'}>Rent</a>
    <a  onClick={()=>setAct("buy btnactive")} className={(act=="buy btnactive")?'btn btn-flat  side-btnactive':'btn btn-flat  black-text'}>Buy</a>
    </div>
             <div className=''>
            <div className='valign-wrapper side'>
                <i className='material-icons left purple-text'>location_on</i>
            <select className='selectbar' onChange={(e)=>setCity(e.target.value)}>
                <option value="">Select City</option>
                <option value="patna">Patna</option>
                <option value="jabalpur">Jabalpur</option>
            </select>
            </div>
            <div className='side'>
            <input type="text" id="search" className='searchbar' placeholder="Search locality, Area, Pincode..."   onChange={(e)=>setSearch(e.target.value)} ref={searchRef}/>
            
            </div>
            <div className='' style={{paddingRight:"0px"}}>
            <button type="submit" onClick={onSearch} className='btn btn-flat black white-text search-btn searchbtnf' id="submit">
                <i className='material-icons search-icon sf-icon circle search-iconf left'>search</i><a className='hide'>Search</a>
                </button>
            </div>
            </div>
            
        </div>
        <div className=''>
            <div className={res}>
                <div className='card-panel side-result-panel row'>
                <ul className='collection col s12 m12 no-border'>
                {filtered.length>0?filtered.slice(0,5).map((item,index)=>{
                    return (
                        <li className='collection-item locality no-border' key={index} onClick={(e)=>{
                            searchRef.current.value = item.name;
                            setValue(searchRef.current.value)
                            setSearch("");
                            setRes("no-result")
                            console.log(item)
                        }}>
                            
                            {item.name}</li>
                    )
                }):<p>no result</p>}
                </ul>
                </div>
                </div>
                
            </div>
        </div>
    )





}