import React, { Component } from 'react';


export default function Loginc(){
    return(
        <div className='container'>
                    <div className='row login_container'>
                        <div className='col s12 m12'>
                            <h6 className='login_header'>Login</h6>
                        </div>
                        <div className='col s12 m12'>
                            <div className='container'>
                                <div class="chip black white-text">
                                    Error Messages and other msgs
                                    <i class="close material-icons">close</i>
                                </div>
                            </div>
                        </div>

                        <div className='col s12 m12'>
                            <div className='login_number '>
                                <input type="text" className='login_no' placeholder='Enter your phone number'/>
                            </div>
                            
                            
                        </div>
                        <br></br>
                        <div className='col s12 m12'>
                            <div className='login_number'>
                                <input type="text" className='login_no' placeholder='Enter your OTP'/>
                            </div>
                            
                            
                        </div>
                       

                        
                        <div className='col s12 m12'>
                            <div className='container'>
                                <p>By accepting, you agree with terms and conditions of rentobro.</p>
                            </div>
                        </div>
                        <div className='col s12 m12 no-pad'>
                                    <div className='container'>
                                        <div class="switch">
                                            <label className=' black-text'>
                                            
                                            <input type="checkbox"/>
                                            <span class="lever"></span>
                                            Accept
                                            </label>
                                        </div>
                                    </div>
                        </div>
                        <div className='col s12 m12 btn-submit-con center'>
                            <div className=''>
                                <button className='btn btn-flat rounded-corners black white-text large login_sub'>Submit</button>
                            </div>
                        </div>
                        <a className='p-s-b-label'></a>
                    </div>
               </div>
    )
}