import './App.css';
import Home from './pages/home';
import { BrowserRouter, Switch,Route } from 'react-router-dom';
import List from './pages/List';
import View from './pages/view';
import Details from './pages/details';
import Add from './pages/add';
import Addlist from './pages/addlist';
import Listconfirm from './pages/listconfirm';
import Logvisit from './pages/logvisit';
import Addproperty from './pages/addproperty';
import Login from './pages/login';
import Logout from './pages/logout';
import Buy from './pages/buy';
import Modsearch from './components/modsearch';
import LoginV from './pages/login_view';
import Au from './pages/adminuser';
import Tu from './pages/tenant';
import About from './pages/about';
import Career from './pages/career';
import Contact from './pages/contact';
import Error from './pages/error';
import Test from './pages/test';
import Blogread from '../src/layouts/blogs';
import Registration from '../src/pages/registration';
import Payments from '../src/pages/payments';





function App() {
  return (
   <BrowserRouter>
   <Switch>
      <Route exact path="/" component={Home}/>
      <Route exact path="/register" component={Registration}/>
      <Route exact path="/payments" component={Payments}/>
      <Route path="/list" component={List}/>
      <Route path="/view" component={View}/>
      <Route path="/add" component={Add}/>
      <Route path="/addlist" component={Addlist}/>
      <Route path="/confirm" component={Listconfirm}/>
      <Route path="/logvisit" component={Logvisit}/>
      <Route path="/addproperty" component={Addproperty}/>
      <Route path="/login" component={Login}/>
      <Route path="/logout" component={Logout}/>
      <Route path="/buy" component={Buy}/>
      <Route path="/modsearch" component={Modsearch}/>
      <Route path="/loginview" component={LoginV}/>
      <Route path="/details" component={Details}/>
      <Route path="/adminuser" component={Au}/>
      <Route path="/dashboard/tenant" component={Tu}/>
      <Route path="/about" component={About}/>
      <Route path="/career" component={Career}/>
      <Route path="/contact" component={Contact}/>
      <Route path="/error" component={Error}/>
      <Route path="/test" component={Test}/>
      <Route path="/blog/read" component={Blogread}/>
  </Switch>
   </BrowserRouter>
  );
}

export default App;
