import React, { Component } from 'react';
import { useHistory } from 'react-router-dom';
import { useEffect, useRef, useState } from "react";
import Sidesearch from './sidesearch';






export default function Footnav(){
const navigate = useHistory()
    const handleSearch=()=>{
        navigate.push({
            pathname:"/modsearch",
            
        })
    }
    const [modal,setmodal] = useState("");

const handleModal =(num)=>{
    if(num === "1"){
        setmodal("search")
    }
    if(num === "2"){
        setmodal("filter")
    }
    if(num === "3"){
        setmodal("account")
    }
}

const [show,setshow] = useState(true)
const [lastscrolly,setlastscrolly] = useState(0);
console.log(window.scrollY)
const controlNavbar=()=>{
   
        if(window.scrollY > lastscrolly){
            //scroll "down" hide navabar
             setshow(false)
        }else{
            setshow(true)
        }
        // remember current page location 
        setlastscrolly(window.scrollY)
        console.log(lastscrolly)
}
useEffect(() => {
    if (typeof window !== 'undefined') {
      window.addEventListener('scroll', controlNavbar);

      // cleanup function
      return () => {
        window.removeEventListener('scroll', controlNavbar);
      };
    }
  }, [lastscrolly]);



    return(
        <div>
            <div className={`bottom-nav black ${show==false?'hidden':'ok'}`}>
            <div className='row '>
                <div className='col s4 center white-text'>
                    <button className='btn btn-flat btmbtntext white-text' onClick={()=>{handleSearch()}}>
                        <i className='material-icons btmbtnicon'>search</i>
                        search
                    </button>
                </div>
                <div className='col s4 center white-text'>
                    <button className='btn btn-flat  btmbtntext white-text modal-trigger' data-target='modal1' onClick={()=>{handleModal("2")}}>
                        <i className='material-icons btmbtnicon'>filter_list</i>
                       filter
                    </button>
                </div>
                <div className='col s4 center white-text'>
                    <button className='btn btn-flat  btmbtntext white-text modal-trigger' data-target='modal1' onClick={()=>{handleModal("3")}}>
                        <i className='material-icons btmbtnicon'>perm_identity</i>
                        profile
                    </button>
                </div>
                     
            </div>
            </div>
        
         <div id="modal1" class="modal">
         <div class="modal-content">
             <i className='material-icons modal-close'>close</i>
         <Sidesearch/>
         </div>
     </div>
     </div>
    )
}