import React, { Component } from 'react';
import Search from '../pages/sidesearch'
import {useHistory} from 'react-router-dom';
import M from 'materialize-css/dist/js/materialize.min.js';
import 'materialize-css/dist/css/materialize.min.css';
import { useEffect, useRef, useState } from "react";






export default function Modfilter(){

    useEffect(()=>{
        var el = document.querySelector('.tabs')
        M.Tabs.init(el,{});
        var elem = document.querySelector('.modal')
        M.Modal.init(elem,{});
        var elems = document.querySelectorAll('.collapsible');
         M.Collapsible.init(elems, {});
    },[])




    const navi = useHistory();
    const handleClose=()=>{
        navi.push({
            pathname:"/list"
        })
    }
    return (
        <div className='section white '>
           
            <div className='container'>
            <div className='filter-container'>
                        <div className='filter-title'>
                            <a className='black-text fil-title'>
                                <i className='material-icons left'>filter_list</i>
                                Filter</a>
                        </div>
                    </div>
                    <ul class="collapsible filter-collap">
                        <li>
                            <div class="collapsible-header"><i class="material-icons left">all_inclusive</i>Tenant <i className='material-icons right-icon'>expand_more</i></div>
                            <div class="collapsible-body">
                            <button className='filbtn'>male</button>
                            <button className='filbtn'>female</button>
                            <button className='filbtn'>any</button>
                            </div>
                        </li>
                        <li>
                            <div class="collapsible-header "><i class="material-icons">place</i>Furnishing<i className='material-icons right-icon'>expand_more</i></div>
                            <div class="collapsible-body">
                            <button className='filbtn'>Full</button>
                            <button className='filbtn'>Semi</button>
                            <button className='filbtn'>non</button>
                            </div>
                        </li>
                        <li>
                            <div class="collapsible-header"><i class="material-icons">whatshot</i>Bedrooms<i className='material-icons right-icon'>expand_more</i></div>
                            <div class="collapsible-body">
                            <button className='filbtn'>Full</button>
                            <button className='filbtn'>Semi</button>
                            <button className='filbtn'>non</button>
                            <button className='filbtn'>Full</button>
                            <button className='filbtn'>Semi</button>
                            <button className='filbtn'>non</button>

                            </div>
                        </li>
                        </ul>
                        <div className='center btn-filter-con'>
                                <button className='btn btn-flat btn-filter-mod black white-text'>Apply(2)</button>
                            </div>
           </div>
            
        </div>
    )
}