import React, { Component } from 'react';
import Logo from '../components/logo';
import Pu from '../assests/img/pushpa.jpg';
import { useEffect, useRef, useState } from "react";
import axios from "axios";
import 'materialize-css/dist/css/materialize.min.css';
import M from 'materialize-css/dist/js/materialize.min.js';

export default function Lognav(props){

const arrr = ["my profile","explore proerty","etc"]



    const name = props.logname;
    useEffect(() => {
        var sidenav = document.querySelectorAll(".sidenav");
        M.Sidenav.init(sidenav, {edge:'right'});
      }, []);
    return (
            <div>
             <nav className='navmain'>
            <div class="nav-wrapper row fixednav">
                <div className=''>
                    <div className='col s2 m4  logocon left'>
                    <Logo/>
                    </div>
                    </div>
                    <ul id="nav-mobile" class="right lognav_ul">
        <li>
            <div className='nav_icon_profile'>
                <img src={Pu} className="nav_icon_image" />
            </div>
            </li>
        <li><a className='black-text nav_profile'>{name}</a></li>
        <li><i href="#" data-target="slide-out" className='material-icons black-text large sidenav-trigger'>apps</i></li>
      </ul>
            </div>
        </nav>
        <ul id="slide-out" class="sidenav right blue-grey lighten-5">
            {arrr.map((item)=>{return(
                <li className='prof-li'>
                <a className='profile-li black-text'>{item}</a>
                </li>
            )})}
            <li className='prof-li'>
                <button className='btn btn-flat black white-text'>logout</button>
                </li>
                
  </ul>
        </div>
    )
}