import Carousel from "react-multi-carousel";
import Data from './simil_data';

const responsive = {
  desktop: {
    breakpoint: { max: 3000, min: 1024 },
    items: 3,
    paritialVisibilityGutter: 60
  },
  tablet: {
    breakpoint: { max: 1024, min: 464 },
    items: 2,
    paritialVisibilityGutter: 50
  },
  mobile: {
    breakpoint: { max: 464, min: 0 },
    items: 1,
    paritialVisibilityGutter: 30
  }
};

console.log(Data)
// Because this is an inframe, so the SSR mode doesn't not do well here.
// It will work on real devices.
const Simplar = ({ deviceType }) => {
  return (
    <Carousel
      ssr
      partialVisbile
      deviceType={deviceType}
      itemClass="image-item"
      responsive={responsive}
    >
      {Data.map(i => {
        return (
            <div className="card  similar_panel">
                
                    <div className="card-image">
                        <img src={i.img} className="similar_img"/>
                        <span className="card-title  similar_title">{i.locality}</span>
                   
                    
                        <button className="btn btn-small amber black-text right">View</button>
                    </div>
            </div>
        );
      })}
    </Carousel>
  );
};

export default Simplar;