import React, { Component } from 'react';
import Lognav from './lognav';
import Hornav from './hormenu';
import { useEffect, useRef, useState } from "react";
import Auprop from '../layouts/auhome';
import Footer from '../components/adminfooter';
import Visits from '../layouts/auvisits';
import Service from '../layouts/auservices';
import Tenant from '../layouts/autenant';
import Account from '../layouts/auaccounts';


export default function Au(){

const [div,setdiv] = useState(<Auprop/>);

const handleDiv = (wrd) =>{
    switch(wrd){
        case("home"):
            return setdiv(<Auprop/>)
            break;
        case("money"):
            return setdiv(<Visits/>)
            break;
        case("plus"):
            return setdiv(<Service/>)
            break;
        case("people"):
            return setdiv(<Tenant/>)
            break;
        case("property"):
            return setdiv(<Account/>)
            break;
    }
      
   
}

    return (
        <div>
            <Lognav logname={"amit"}/>
            <div className=' hormenu_section_au '>
            <Hornav onClick={handleDiv}/>
            </div>
            <div className='section holder'>
                <div className='container'>
                {div}
                </div>
                
             </div>
             
          
            <Footer/>
           
        </div>
    )
}