import React, { Component } from 'react';
import Lognav from './lognav';
import Hornav from './hormenutenant';
import { useEffect, useRef, useState } from "react";
import Teprop from '../layouts/tehome';
import Footer from '../components/adminfooter';
import Visits from '../layouts/auvisits';
import Service from '../layouts/auservices';
import Tenant from '../layouts/autenant';
import Account from '../components/teaccounts';
import Payment from '../components/expand';

export default function Te(props){


    const [div,setdiv] = useState(<Teprop/>);

const handleDiv = (wrd) =>{
    switch(wrd){
        case("home"):
            return setdiv(<Teprop/>)
            break;
        case("money"):
            return setdiv(<Account/>)
            break;
        case("plus"):
            return setdiv(<Service/>)
            break;
        case("people"):
            return setdiv(<Tenant/>)
            break;
        case("property"):
            return setdiv(<Account/>)
            break;
        case("payment"):
            return setdiv(<Payment/>)
            break;
    }
      
   
}

    return (
        <div>
            <Lognav logname={"amit"}/>
            <div className=' hormenu_section_au '>
            <Hornav onClick={handleDiv}/>
            </div>
            <div className='section holder'>
                <div className='container'>
                {div}
                </div>
             </div>
          
            <Footer/>
           
        </div>
    )
}