import React, { Component } from 'react';
import Input from '../components/input'
import Block from '../components/blockselect'
export default function Residential(props){
    const type = props.type
    const furn = "un"
    const handleBlock=(item)=>{
        alert(item)
    }
    return (
        <div>
           <div className="rent">
            <div className='adp_container'>
                <div className='adp_header_con'>
                    <h6 className='adp_header'>Add Your Rental Property Details</h6>
                </div>
                <div className="property_detail">
                <div className='adp_label_con'>
                    <h6 className='adp_label black-text'>Property location Details</h6>
                </div>
                <div className='adp_label_con'>
                    <h6 className='adp_label'>Locality</h6>
                </div>
                <div className='adp_input_con'>
                    <Input placeholder={"Enter locality pincode"} className={"adp_input"}/>
                </div>
                <div className='adp_label_con'>
                    <h6 className='adp_label'>City</h6>
                </div>
                <div className='adp_input_con'>
                    <Input placeholder={"Enter City"} className={"adp_input"}/>
                </div>
                <div className='adp_label_con'>
                    <h6 className='adp_label'>Address Line 1</h6>
                </div>
                <div className='adp_input_con'>
                    <Input placeholder={"Enter Address Line 1"} className={"adp_input"}/>
                </div>
                <div className='adp_label_con'>
                    <h6 className='adp_label'>Address Line 2</h6>
                </div>
                <div className='adp_input_con'>
                    <Input placeholder={"Enter Address Line 2"} className={"adp_input"}/>
                </div>
                <div className='adp_label_con'>
                    <h6 className='adp_label'>Built Up Area</h6>
                </div>
                <div className='adp_input_con'>
                    <Input placeholder={"Enter built up area"} className={"adp_input"}/>
                </div>
                <div className='adp_label_con'>
                    <h6 className='adp_label'>Carpet Area</h6>
                </div>
                <div className='adp_input_con'>
                    <Input placeholder={"Enter carpet area"} className={"adp_input"}/>
                </div>
                </div>
                <div className='adp_label_con'>
                    <h6 className='adp_label'>Property Type</h6>
                </div>
                <div className='adp_block_con'>
                    <Block onChange={handleBlock} options={["Flat/Apartment","MultiStorey","Villa","Penthouse","Studio Apartment","Pg/Co living"]}/>
                </div>
                <div className='adp_label_con'>
                    <h6 className='adp_label'>Age of Property</h6>
                </div>
                <div className='adp_block_con'>
                    <Block onChange={handleBlock} options={["1R","1RK","1BHK","2BHK","3BHK","4BHK","5BHK","6BHK"]}/>
                </div>
                <div className='adp_label_con'>
                    <h6 className='adp_label'>Bathrooms</h6>
                </div>
                <div className='adp_block_con'>
                    <Block onChange={handleBlock} options={["1","2","3","4","5","6","7"]}/>
                </div>
                <div className='adp_label_con'>
                    <h6 className='adp_label'>Balcony</h6>
                </div>
                <div className='adp_block_con'>
                    <Block onChange={handleBlock} options={["1","2","3+"]}/>
                </div>
                <div className='adp_label_con'>
                    <h6 className='adp_label'>Furnishing</h6>
                </div>
                <div className='adp_block_con'>
                    <Block onChange={handleBlock} options={["Fully Furnished","Semi furnished","Unfurnished"]}/>
                </div>
                {furn === "un"?
                <div>
                    <div className='adp_label_con'>
                    <h6 className='adp_label'>Add Furnishing</h6>
                </div>
                <div className='adp_block_con'>
                    <Block onChange={handleBlock} options={["Dining Table"," washing Machine"," sofa"," microwave"," stove","fridge", "water", "Purifier", "AC"," bed"," Tv","Cupboards","Geyser", "Bed", "kitchen", "cabinet", "mattress" ]}/>
                </div>
                </div>:
                <div>
                </div>}
                <div className='adp_label_con'>
                    <h6 className='adp_label'>Society Amenities</h6>
                </div>
                <div className='adp_block_con'>
                    <Block onChange={handleBlock} options={["Lift"," CCTV"," Gym"," garden"," kids"," area"," sports"," area","swimming"," pool"," intercom"," gated"," community"," club"," house","community"," mall"," regular"," water"," supply"," power backup"," pet allowed"]}/>
                </div> 
                <div className='adp_label_con'>
                    <h6 className='adp_label'>Covered Parking</h6>
                </div>
                <div className='adp_block_con'>
                    <Block onChange={handleBlock} options={["0","1","2","3","3+"]}/>
                </div> 
                <div className='adp_label_con'>
                    <h6 className='adp_label'>Open Parking</h6>
                </div>
                <div className='adp_block_con'>
                    <Block onChange={handleBlock} options={["0","1","2","3","3+"]}/>
                </div> 
                <div className='adp_label_con'>
                    <h6 className='adp_label'>Preffered Tenant Type</h6>
                </div>
                <div className='adp_block_con'>
                    <Block onChange={handleBlock} options={["Family","Bachelors","Company"]}/>
                </div> 
                <div className='adp_label_con'>
                    <a className='btn btn-flat btn-large black white-text'>Save and Proceed</a>
                </div>
            </div>
           </div>
           <div className="sell">
            
            </div>
        </div>
    )
}