import React, { Component } from 'react';
import { useLocation } from 'react-router-dom';
import { useHistory } from 'react-router-dom';
import Nav from '../components/nav'
import Data from '../data/view';
import M from 'materialize-css/dist/js/materialize.min.js';
import 'materialize-css/dist/css/materialize.min.css';
import { useEffect, useRef, useState } from "react";
import DATA_QUES from './ques';
import data from './ques';


export default function App(){
    const loco = "list";
    const fixed = "fixed";

   

const [prop_num,setpropNum] = useState("");
const [prop_num_cl,setpropNumCl] = useState("valid hide");
const [prop_loca,setpropLoca] = useState("");
const [prop_loca_cl,setpropLocaCl] = useState("valid hide");
const [same_city,setSm] = useState("");
const [same_city_cl,setSmCl] = useState("valid hide");
const [o_name,setName] = useState("");
const [o_name_cl,setNameCl] = useState("valid hide");
const [purpose,setPurpose] = useState("");
const [purpose_cl,setPurposeCl] = useState("valid hide");
const [p_num,setPhone] = useState("");
const [p_num_cl,setPhoneCl] = useState("valid hide");

const [quesno,setQuesNo] = useState(1);
const [ques,setQues] = useState(data[1].q)
const [opt,setopt] = useState(data[1])
const [id,setId] = useState(1)

const handleOpt = (ans,curr) =>{
    let num = ans+1
    if(ans===1){
        setpropNum(curr)
        setpropNumCl("valid")
    }if(ans===2){
        setSm(curr)
        setSmCl("valid")
    }if(ans===3){
        setpropLoca(curr)
        setpropLocaCl("valid")
    }if(ans===4){
        setPurpose(curr)
        setPurposeCl("valid")
        handleView()

    }if(ans===5){
        
    }if(ans===6){
        
    }





    setId(num)
    setQues(data[num].q)
    setopt(data[num])

   
   }
const handleName=()=>{
    handleOpt(4)
}



const navigate = useHistory();
const handleView = () => {
    navigate.push({
        pathname:"/addlist",
        propnum : prop_num,
        locaprop:prop_loca,
        ownloca:same_city,
        purp:purpose


        
    })
}









    return (

            <div>
                <Nav/>    
               <div className='section'>
                <div className='row'>
                    <div className='chatbot_con'>
                        <div className='col s12 m5 hide-on-med-and-down lime darken-2 chatbot_con_left'>
                            <div className='row'>
                                <div className='col s12 m12 chat_left_box'>
                                    <h6 className='chatbot_con_head white-text'>
                                        How is Rentobro different?
                                    </h6>
                                    <p>.</p>
                                </div>
                       
                       
                                <div className='col s12 m12 '>
                                <div class="offset-m2 l6 offset-l3">
                                                <div class="card-panel chatbot_card  z-depth-1">
                                                <div class="row valign-wrapper">
                                                    <div class="col s2">
                                                    <img src="images/yuna.jpg" alt="" class="circle responsive-img blue-grey lighten-4"/> 
                                                    </div>
                                                    <div class="col s10">
                                                    <blockquote class="black-text text-accent-1">
                                                        This is a square image. Add the "circle" class to it to make it appear circular.
                                                    </blockquote>
                                                    </div>
                                                </div>
                                                </div>
                                            </div>
                                </div>
                                <div className='col s12 m12 '>
                                <div class="offset-m2 l6 offset-l3">
                                                <div class="card-panel chatbot_card  z-depth-1">
                                                <div class="row valign-wrapper">
                                                    <div class="col s2">
                                                    <img src="images/yuna.jpg" alt="" class="circle responsive-img blue-grey lighten-4"/> 
                                                    </div>
                                                    <div class="col s10">
                                                    <blockquote class="black-text text-accent-1">
                                                        This is a square image. Add the "circle" class to it to make it appear circular.
                                                    </blockquote>
                                                    </div>
                                                </div>
                                                </div>
                                            </div>
                                </div>
                                
                            </div>
                        </div>
                        <div className='col s12 m7'>
                            <div className='col s12 m12 asked_con'>
                            <div className='col s12 m12'>
                                    <div className={prop_num_cl}>
                                    <a className='ques-valid left black-text'>No. of properties</a>
                                    <a className='right chip green accent-1 black-text'>{prop_num}</a>
                                    </div>
                                    
                                </div>
                                <div className='col s12 m12'>
                                    <div className={prop_loca_cl}>
                                    <a className='ques-valid left black-text'>location of properties</a>
                                    <a className='right chip green accent-1 black-text'>{prop_loca}</a>
                                    </div>
                                    
                                </div>
                                <div className='col s12 m12'>
                                    <div className={same_city_cl}>
                                    <a className='ques-valid left black-text'>location of owner</a>
                                    <a className='right chip green accent-1 black-text'>{same_city}</a>
                                    </div>
                                    
                                </div>
                                <div className='col s12 m12'>
                                    <div className={purpose_cl}>
                                    <a className='ques-valid left black-text'>purpose</a>
                                    <a className='right chip green accent-1 black-text'>{purpose}</a>
                                    </div>
                                <div className='col s12 m12'>
                                    <div className={o_name_cl}>
                                    <a className='ques-valid left black-text'>owner name</a>
                                    <a className='right chip green accent-1 black-text'>{o_name}</a>
                                    </div>
                                    
                                </div>
                                
                                    
                                </div>
                                <div className='col s12 m12'>
                                    <div className={p_num_cl}>
                                    <a className='ques-valid left black-text'>Phone number</a>
                                    <a className='right'>{p_num}</a>
                                    </div>
                                    
                                </div>
                            </div>
                            <div className='col s12 m12 ask_con'>
                                <div className='col s12 m12 ques_con'>
                                {ques}
                                </div>
                                <div className='col s12 m12 opt_con valign-wrapper'>
                                    
                                    {opt.o.map((o)=>{
                                        console.log(id)
                                        if(opt['o-t']==='btn'){
                                            let curr = o
                                            return <div className=''>
                                                <button onClick={()=>handleOpt(id,curr)} className='btn btn-flat white btn-opt'>{curr}</button>
                                            </div>
                                        }if(opt['o-t']==='input_name'){
                                            return(<div>
                                                <input placeholder='enter name'/>
                                                <button onClick={handleName()}>submit</button>
                                            </div>)
                                        }if(opt['o-t']==='input'){
                                            return(<div></div>)
                                        }
                                    })}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                
                </div>     
             </div>


    )
}